.taskbar {
    position: relative;
}

.start {
    position: relative;
    user-select: none;

    .toggle {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        cursor: pointer;
        opacity: 0;
    }

    .toggle:active+.button {
        @include border-invert();
    }

    .toggle:checked+.button+.menu {
        display: block;
    }

    .menu {
        position: absolute;
        bottom: 100%;
        left: 0;
        background: $startbg;
        margin: 0;
        padding: 0;
        @include border();
        display: none;

        .item {
            padding: 0.5em 3em;
            list-style: none;
            cursor: pointer;

            &:hover {
                background: $startfocus;
                color: #fff;
            }
        }
    }
}