@import './_partial/colors.scss';
@import './_partial/button.scss';
@import './_partial/taskbar.scss';
@import './_partial/window.scss';
@import './_partial/start.scss';

body,
html {
    width: 100%;
    height: 100%;
}

body {
    background: $background;
    margin: 0;
    display: -webkit-box;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    flex-direction: column;
    font-family: MS Sans Serif, sans-serif;
    overflow: hidden;
}

.main-area {
    -webkit-box-flex: 2;
    flex-grow: 2;
    display: -webkit-box;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
}